import ListItem from '@/components/ListItem';
import TransactionStatus from '@/components/TransactionStatus';
import NoTransactionsFound from '@/components/NoTransactionsFound.vue';
import { getStep, ACTIVITY_STATUSES, ACTIVITY_FILTER_TYPES, SEND_STATUS_FILTER_MAP } from '@liquality/wallet-core/dist/src/utils/history';
import { getSwapProvider } from '@liquality/wallet-core/dist/src/factory';
import { getItemIcon } from '@/utils/history';
import { prettyBalance, prettyFiatBalance, formatFiatUI } from '@liquality/wallet-core/dist/src/utils/coinFormatter';
import moment from '@liquality/wallet-core/dist/src/utils/moment';
import { mapState } from 'vuex';
export default {
  components: {
    ListItem,
    TransactionStatus,
    NoTransactionsFound
  },
  props: ['transactions'],
  computed: {
    ...mapState(['fiatRates'])
  },
  methods: {
    getItemIcon,
    prettyBalance,
    prettyFiatBalance,
    formatFiatUI,
    getTitle(item) {
      switch (item.type) {
        case 'SWAP':
          return `${item.from} to ${item.to}`;
        case 'SEND':
          return `Send ${item.from}`;
        case 'NFT':
          return item.status === 'SUCCESS' ? `Sent NFT` : `Send NFT`;
        case 'RECEIVE':
          return `Receive ${item.from}`;
        default:
          return '';
      }
    },
    getSubTitle(item) {
      return moment(item.startTime).format('L, LT');
    },
    getDetail(item) {
      const amount = item.type === 'SWAP' ? item.fromAmount : item.amount;
      if (!amount) return `${item.from}`;
      return `${this.prettyBalance(amount, item.from)} ${item.from}`;
    },
    getDetailSub(item) {
      const status = this.getUIStatus(item);
      if (status) {
        const filterStatus = ACTIVITY_STATUSES[status];
        if (filterStatus) {
          return filterStatus.label;
        }
      }
      return '';
    },
    getUIStatus(item) {
      if (item.type === 'NFT') {
        return SEND_STATUS_FILTER_MAP[item.status];
      } else if (item.type === 'SEND') {
        return SEND_STATUS_FILTER_MAP[item.status];
      } else if (item.type === 'SWAP') {
        const swapProvider = getSwapProvider(item.network, item.provider);
        return swapProvider.statuses[item.status].filterStatus;
      }
    },
    getDetailsUrl(item) {
      return {
        NFT: `/details/nft-transaction/${item.id}`,
        SEND: `/details/transaction/${item.id}`,
        SWAP: `/details/swap/${item.id}`
      }[item.type];
    },
    getTypeIcon(type) {
      const filter = ACTIVITY_FILTER_TYPES[type];
      return this.getItemIcon(filter === null || filter === void 0 ? void 0 : filter.icon);
    },
    getTransactionStep(item) {
      return getStep(item) + 1;
    },
    getTotalSteps(item) {
      switch (item.type) {
        case 'SEND':
          return 2;
        case 'SWAP':
          {
            const swapProvider = getSwapProvider(item.network, item.provider);
            return swapProvider.totalSteps;
          }
        default:
          return 0;
      }
    },
    getCompletedAmount(item) {
      const amount = item.type === 'SWAP' ? item.fromAmount : item.amount;
      if (!amount) return '';
      return !this.fiatRates[item.from] ? '' : `${formatFiatUI(prettyFiatBalance(prettyBalance(amount, item.from), this.fiatRates[item.from]))}`;
    }
  }
};