import "core-js/modules/es.array.push.js";
import CalendarIcon from '@/assets/icons/calendar.svg';
import ChevronDownIcon from '@/assets/icons/chevron_down.svg';
import CloseIcon from '@/assets/icons/close.svg';
import ExportIcon from '@/assets/icons/export.svg';
import ListItem from '@/components/ListItem';
import { ACTIVITY_FILTER_TYPES, ACTIVITY_STATUSES } from '@liquality/wallet-core/dist/src/utils/history';
import { getItemIcon } from '@/utils/history';
import DatePick from 'vue-date-pick';
import '@/assets/scss/vue-date-pick.scss';
import { getCSVContent } from '@liquality/wallet-core/dist/src/utils/export';
import { downloadFile } from '@/utils/export';
const CSV_HEADERS = [{
  label: 'ID',
  key: 'id'
}, {
  label: 'Network',
  key: 'network'
}, {
  label: 'Created',
  key: 'startTime'
}, {
  label: 'From Asset',
  key: 'from'
}, {
  label: 'To Asset',
  key: 'to'
}, {
  label: 'Send Amount',
  key: 'fromAmount'
}, {
  label: 'Receive Amount',
  key: 'toAmount'
}, {
  label: 'Swap Tx HASH',
  key: 'swapTxHash'
}, {
  label: 'Status',
  key: 'status'
}, {
  label: 'Wallet ID',
  key: 'walletId'
}];
export default {
  props: ['activityData', 'showTypeFilters'],
  components: {
    CalendarIcon,
    ChevronDownIcon,
    CloseIcon,
    ExportIcon,
    ListItem,
    DatePick
  },
  data() {
    return {
      headers: [...CSV_HEADERS],
      open: false,
      dateFilters: {
        start: null,
        end: null
      },
      statusFilters: {
        ...ACTIVITY_STATUSES
      },
      typeFilters: {
        ...ACTIVITY_FILTER_TYPES
      }
    };
  },
  computed: {
    filterCount() {
      const count = this.typeFiltersCount + this.statusFiltersCount;
      if (this.dateFilters.start !== null || this.dateFilters.end !== null) {
        return count + 1;
      }
      return count;
    },
    typeFiltersCount() {
      return Object.values(this.typeFilters).filter(f => f.selected === true).length;
    },
    statusFiltersCount() {
      return Object.values(this.statusFilters).filter(f => f.selected === true).length;
    }
  },
  methods: {
    getCSVContent,
    getItemIcon,
    toggleTypeFilter(key) {
      if (key in this.typeFilters) {
        this.typeFilters[key].selected = !this.typeFilters[key].selected;
        this.typeFilters = {
          ...this.typeFilters
        };
        this.applyFilters();
      }
    },
    toggleStatusFilter(key) {
      if (key in this.statusFilters) {
        this.statusFilters[key].selected = !this.statusFilters[key].selected;
        this.statusFilters = {
          ...this.statusFilters
        };
        this.applyFilters();
      }
    },
    exportActivity() {
      const content = this.getCSVContent(this.activityData, this.headers);
      downloadFile({
        filename: 'activity.csv',
        type: 'text/csv;charset=utf-8;',
        content
      });
    },
    resetFilters() {
      this.dateFilters = {
        start: null,
        end: null
      };
      for (const key in this.typeFilters) {
        this.typeFilters[key].selected = false;
      }
      for (const key in this.statusFilters) {
        this.statusFilters[key].selected = false;
      }
      this.typeFilters = {
        ...this.typeFilters
      };
      this.statusFilters = {
        ...this.statusFilters
      };
      this.applyFilters();
    },
    applyFilters() {
      const types = [];
      const statuses = [];
      for (const key in this.typeFilters) {
        if (this.typeFilters[key].selected === true) {
          types.push(key);
        }
      }
      for (const key in this.statusFilters) {
        if (this.statusFilters[key].selected === true) {
          statuses.push(key);
        }
      }
      this.$emit('filters-changed', {
        types,
        statuses,
        dates: this.dateFilters
      });
    },
    changeTrigered() {
      this.$nextTick(() => {
        this.applyFilters();
      });
    }
  }
};